<template>
  <view class="order-list">
    <view v-for="order in orders" :key="order.id" class="order-item">
      <text>订单 #{{ order.id }}：{{ order.status }}</text>
      <view class="actions">
        <button @click="updateStatus(order.id, '制作中')">接单</button>
        <button @click="updateStatus(order.id, '已完成')">出餐</button>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      orders: [
        { id: 101, status: '待接单' },
        { id: 102, status: '制作中' }
      ]
    }
  },
  methods: {
    updateStatus(id, status) {
      // TODO: 更新订单表
      const order = this.orders.find(o => o.id === id)
      if (order) order.status = status
      uni.showToast({ title: `状态已更新为 ${status}` })
    }
  }
}
</script>
